<template>
    <div>
        <template v-if="field.fixed || !designer">
            <!-- eslint-disable vue/no-v-html -->
            <p v-html="model" />
            <!--eslint-enable-->
        </template>
        <v-sheet v-else>
            <v-label>{{ field.label }}</v-label>
            <ckeditor v-model="model" :editor="ClassicEditor" :config="config" />
        </v-sheet>
    </div>
</template>

<script setup>
import { computed, defineModel } from 'vue';
import {
    ClassicEditor,
    Essentials,
    Paragraph,
    Bold,
    Italic,
    Underline,
    FontSize,
    Alignment,
    List,
    AutoLink,
    Link,
} from 'ckeditor5';
import { Ckeditor } from '@ckeditor/ckeditor5-vue';
import 'ckeditor5/ckeditor5.css';

const props = defineProps({
    field: { type: Object, required: true },
    disabled: { type: Boolean, default: false },
    designer: { type: Boolean, default: false },
});
const model = defineModel();
const config = computed(() => {
  return {
    licenseKey: 'GPL',
    plugins: [
        Essentials,
        Paragraph,
        Bold,
        Italic,
        Underline,
        FontSize,
        Alignment,
        List,
        Link,
        AutoLink,
    ],
    toolbar: [
        'undo',
        'redo',
        '|',
        'fontSize',
        'alignment',
        '|',
        'bold',
        'italic',
        'underline',
        '|',
        'numberedList',
        'bulletedList',
        '|',
        'link',
    ],
  };
});

function reviseLinkURLs(text) {
  if (this.designer) {
    const el = document.createElement('html');
    el.innerHTML = text;
    const links = el.getElementsByTagName('a');
    if (links && links.length > 0) {
      Array.prototype.forEach.call(links, (link) => {
        const currentPath = window.location.href.substring(0, window.location.href.lastIndexOf('/') + 1);
        if (link.href.includes(currentPath)) {
          link.setAttribute('href', `http://${link.href.substring(currentPath.length, link.href.length)}`);
        }
      });
      this.text = el.innerHTML;
    }
  }
}
</script>
<style>
.ck-editor__editable_inline {
    min-height: 200px;
}
</style>
