<template>
    <v-menu
        ref="menu"
        v-model="menu"
        :close-on-content-click="false"
        transition="scale-transition"
        offset="40"
        min-width="290px"
    >
        <template #activator="{ props }">
            <v-text-field
                v-model="dateFormatted"
                :rules="rules"
                hint="YYYY-MM-DD"
                prepend-icon="mdi:mdi-calendar"
                :required="field.required || false"
                role=""
                maxlength="10"
                :designer="designer"
                :disabled="designer"
                clearable

                :onkeyup="formatDateValue()"
                @update:model-value="$emit('input', $event)"
                @blur="date = parseDate(dateFormatted)"
                v-bind="props"
                @change="save"
            >
                <template #label>
                    <span
                        v-if="field.required"
                        class="text-red"
                    >
                        <strong>* </strong>
                    </span>
                    {{ field.label + (field.required ? ' (required)' : '') }}
                </template>
            </v-text-field>
        </template>
        <v-date-picker
            v-model="date"
            no-title
            scrollable
            :show-current="true"
            :min="minDate"
            :max="field.dataSource === 'dateOfBirth' ? maxDate : ''"
            @input="menu = false"
            @change="save"
        />
    </v-menu>
</template>

<script>
import { isValid } from "date-fns";

export default {
    name: 'DateOfBirth',
    props: {
        value: { type: String, default: '' },
        field: { type: Object, required: true },
        designer: { type: Boolean, default: false },
    },
    emits: ['input'],
    data: () => ({
        activePicker: null,
        date: null,
        dateFormatted: '',
        menu: false,
        minDate: '1950-01-01',
        maxDate: new Date().toISOString().substr(0, 10),
        rules: [],
    }),
    computed: {
        selected: {
            get() {
                return this.date;
            },
            set(value) {
                this.$emit('input', value);
                this.date = value;
            },
        },
        computedDateFormatted() {
            return this.formatDate(this.date);
        },
    },
    watch: {
        date() {
            this.dateFormatted = this.formatDate(this.date);
        },
        menu(val) {
            if (val) {
                setTimeout(() => {
                    this.activePicker = 'YEAR';
                }, 0);
            }
        },
    },
    mounted() {
        this.date = this.value;
        this.rules.push((v) => (!v || /^(\d{4})-(\d{2})-(\d{2})+$/.test(v)) || 'Date must be valid (YYYY-MM-DD)');
        this.rules.push((v) => (!v || isValid(this.dateFormatted) || 'Please enter a valid date'));
    },
    methods: {
        formatDateValue() {
            const ele = this.dateFormatted;
            if (!ele) return null;
            const x = ele.replace(/\D/g, '').match(/(\d{0,4})(\d{0,2})(\d{0,2})/);
            this.dateFormatted = !x[2] ? x[1] : `${x[1]}-${x[2]}-${x[3]}`;
            return this.dateFormatted;
        },
        formatDate(date) {
            if (!date) return null;
            const [year, month, day] = date.split('-');
            return `${year}-${month}-${day}`;
        },
        parseDate(date) {
            if (!date) return null;
            const [year, month, day] = date.split('-');
            console.log(year, month, day);
            return `${year}-${month}-${day}`;
        },
        save(date) {
            this.$emit('input', date);
            this.$refs.menu.save(date);
        },
    },
};
</script>
